@import '../icon/style.css';
@import 'nprogress/nprogress.css';
@import 'iziToast/dist/css/iziToast.min.css';

:root {
  --theme-background-dicom-viewer: #333333;
  --theme-background-active-dicom-viewer: #4C4C4C;
  --theme-background-hover-dicom-viewer: #1d1d1d;
  --theme-color-dicom-viewer: #fff;
  --theme-color2-dicom-viewer: #297FFF;
}

@ToolbarH: 62px;
@leftPanelW: 162px;

.scrollbar() {
  @scrollbar: {
    webkit: ~"::-webkit-scrollbar";
    moz: ~"::-moz-scrollbar";
    o: ~"::-o-scrollbar";
    ms: ~"::-ms-scrollbar";
    1: ~"::scrollbar";
    2: ~"scrollbar";
  }

  each(@scrollbar, {
    @{value} {
      width: 6px;
      height: 6px;
    }

    :hover@{value} {
      width: 10px;
      height: 10px;
    }

    /*  */
    @{value}-track {
      position: absolute;
      // background-color: rgba(255, 255, 255, 0.9);
      background-color: #f1f1f1;
      border-radius: 100px;
    }

    /*  */
    @{value}-thumb {
      background-color: rgba(0, 0, 0, .5);
      // background-color: rgba(153, 153, 153, 0.8);
      border-radius: 100px;

      &:hover {
        background-color: rgba(0, 0, 0, .6);
        // background-color: rgba(153, 153, 153, 1);
      }
    }

    /*  */
    @{value}-button {
      // background-color:rgba(255, 87, 79, 0);
      width: 0px;
      height: 0px;
    }

    /*  */
    @{value}-corner {
      background-color: transparent;
    }
  })
}

.pointer() {
  cursor: pointer;
}

.hidden {
  display: none !important;
}

.dicomView {
  position: relative;
  display: flex;
  flex-direction: column;
  color: var(--theme-color-dicom-viewer);
  background-color: var(--theme-background-dicom-viewer);
  .scrollbar();

  .iconfont {
    font-family: 'icomoon' !important;
  }

  * {
    margin: 0;
    padding: 0;
  }

  i {
    font-style: normal;
  }

  img {
    display: block;
    max-width: 100%;
  }
}

.dicomViewToolbar {
  position: relative;
  display: flex;
  align-items: center;
  height: @ToolbarH;
  border-bottom: 1px solid #CACACA;

  .dicomViewLogo {
    width: 100px;
    max-height: 100%;
    margin: 17px 18px 17px 24px;
  }

  .divider {
    position: relative;
    display: inline-block;
    height: @ToolbarH;
    width: 1px;
    margin: 0 18px;

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      transform: translateY(-50%);
      width: 1px;
      height: 32px;
      background: #616a7e;
    }
  }

  .toolbar {
    position: relative;
    overflow-y: hidden;
    overflow-x: auto;
    flex: 1;
  }

  .toolsChild {
    display: none;
    white-space: nowrap;

    &.active {
      display: flex;
    }

    .flex-1 {
      flex: 1;
    }

    .triangle {
      position: absolute;
      right: -4px;
      bottom: 0px;
      width: 0;
      height: 0;
      border-bottom: 4px solid var(--theme-color-dicom-viewer);
      border-left: 4px solid transparent;
    }

    .tool-btn {
      display: inline-block;
      padding: 5px 0px 6px;
      margin: 3px 0 5px;
      height: 54px;
      box-sizing: border-box;
      border: 1px solid transparent;
      border-radius: 4px;
      font-size: 13px;
      text-align: center;
      .pointer();

      &.active {
        background: var(--theme-background-active-dicom-viewer)
      }

      &:hover {
        background: var(--theme-background-hover-dicom-viewer);
      }

      .toolbar-text {
        display: block;
        font-size: 12px;
        line-height: 12px;
        padding: 4px 9px 0;
        max-width: 48px;
        box-sizing: content-box;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        text-wrap: nowrap;
      }

      .iconfont {
        position: relative;
        font-size: 26px;
        line-height: 26px;
      }

    }
  }




}

.viewerContentWrapper {
  position: relative;
  display: flex;
  flex: 1;
  overflow: hidden;
}

.viewerLeftSidePanel {
  width: @leftPanelW;
  height: 100%;
  overflow-y: auto;

  .panel-info {
    padding: 0 18px 8px;
    font-size: 14px;
    line-height: 21px;
    border-bottom: 1px solid #7e879b;
    box-sizing: border-box;
    height: 100px;

    >p:nth-child(1) {
      padding: 14px 0 4px;
      font-size: 32px;
      line-height: 32PX;
    }

    >p:nth-child(2) {
      display: flex;
      white-space: nowrap;

      span {
        display: inline-block;
        max-width: 60px;
        overflow: hidden;
        text-overflow: ellipsis;
        text-wrap: nowrap;
      }
    }

  }


  .studyNavigationContainer {
    overflow: hidden;
    transition: 0.75s;
    max-height: 100px;
    .pointer();
    /* seriesjs */
    // &.active {
    //   max-height: calc(100% - 60px);
    // }

    .viewer-navigator-wrapper {
      position: relative;
      box-sizing: border-box;
      height: 120px;
      padding: 0 18px;
      background: var(--theme-background-dicom-viewer);
      overflow: hidden !important;
      border-bottom: 1px solid #7e879b;
      .pointer();

      &.active {
        border-bottom: 1px solid var(--theme-color2-dicom-viewer);
        box-shadow: 0 -1px 1px var(--theme-color2-dicom-viewer);
        background: var(--theme-background-active-dicom-viewer);
      }

      &:hover {
        background: var(--theme-background-hover-dicom-viewer);
      }

      &.showProgress {
        .progressGroup {
          display: block;
        }
      }

      &.loaded {
        .progressGroup {
          display: none;
        }
      }

      .progressGroup {
        display: none;
        background: rgba(0, 0, 0, 0.7);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        right: 0;
        bottom: 0;

        .progressNum {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 20px;
        }

        .progressBar {
          position: absolute;
          bottom: 7px;
          left: 0;
          height: 4px;
          width: 0%;
          background: #fff;
          border-radius: 0 8px 8px 0;
        }
      }
    }

    .navigator-card {
      padding-top: 8px;
      height: 100%;
      box-sizing: border-box;
      background: rgba(0, 0, 0, 0.5)
    }

    .icon-image {
      width: 86px;
      height: 68px;
      margin: 0 17px 4px;
      background-color: #000;
      display: flex;
      flex-direction: column;
      justify-content: center;
      vertical-align: baseline;
      overflow: hidden;
      .pointer();

      img {
        max-width: none;
        width: 86px;
        height: 68px;
        object-fit: contain;
      }
    }

    .navigator-text-wrapper {
      display: inline-block;
      width: 100%;
      text-align: center;
      font-size: 12px;

      .text {
        font-size: 14px;
        line-height: 21px;
      }

      .navigator-description {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        text-wrap: nowrap;
      }
    }
  }

}

.canvasbox {
  position: relative;
  flex: 1;
  overflow: hidden;

  .container {
    position: relative;
    height: 100%;
  }

  .study-slider-layout {
    position: absolute;
    right: 1px;
    top: 0px;
    width: 11px;
    height: 100%;
    z-index: 99;
    overflow-y: auto;

    &:hover {
      &::-webkit-scrollbar {
        width: 10px;
      }

    }
  }

  .series-slider-layout {
    position: absolute;
    right: 0px;
    top: 0px;
    width: 20px;
    height: 100%;
    z-index: 98;
    overflow-y: auto;
  }

  .layoutImage,
  .layoutCellsOuter,
  .layoutCellsInner {
    position: relative;
    box-sizing: border-box;
    margin: 1px;
    border: 2px solid #898989;

    &.active {
      border: 2px dashed rgb(255, 255, 0);
    }
  }

  .layoutCellsOuter {
    &.active {
      border: 2px solid var(--theme-color2-dicom-viewer);
    }
  }

  .layoutCellsInner {
    height: calc(100% - 2px);
    background: #000;

    &.noActiveBorder {
      border-width: 0px;
    }
  }

  .signBigLayout {
    position: absolute;
    z-index: 99;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .seriesSynchronizerLayout {
    position: absolute;
    right: 30px;
    top: 40px;

    .checkbox-input {
      display: none;

      &:checked+.checkbox-label {
        background-color: var(--theme-color2-dicom-viewer);

        .circle {
          left: 35px;
        }

        .on {
          display: inline-block;
        }

        .off {
          display: none;
        }
      }
    }

    .checkbox-label {
      position: relative;
      display: block;
      width: 56px;
      height: 24px;
      background-color: #788195;
      border-radius: 30px;
      color: #fff;

      .text {
        line-height: 24px;
        font-size: 12px;
      }

      .circle {
        position: absolute;
        top: 3px;
        left: 3px;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-color: #fff;
        transition: all 0.3s;
      }

      .on {
        display: none;
        text-indent: 8px;
      }

      .off {
        display: inline-block;
        text-indent: 24px;
      }
    }
  }
}

.select-popup-layout {
  position: fixed;
  background-color: var(--theme-background-dicom-viewer);
  opacity: 0.9;
  z-index: 999;
  box-shadow: 0px 0px 8px #000;
  font-size: 14px;
  text-align: left;

  .select-popup-item {
    .pointer();
    display: flex;
    align-items: center;
    padding: 8px 12px;
    border: 1px solid transparent;
    border-radius: 0;
    line-height: 26px;

    &.active {
      background: var(--theme-background-active-dicom-viewer);
    }

    &:hover {
      background: var(--theme-background-hover-dicom-viewer);
    }

    i {
      font-size: 24px;
      margin-right: 10px;
    }
  }

  .select-grid-item {
    margin: 1px;
    border: 1px solid #585858;
    background: #efefef;
    cursor: pointer;
    .pointer();

    &.active {
      background-color: rgb(198, 198, 198);
      opacity: 0.8;
    }
  }

  .select-grid-item2 {
    margin: 3px;
    .pointer();
  }
}

.popupLayout {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: rgba(0, 0, 0, 0.55);

  .popup-info-layout {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 700px;
    background: var(--theme-background-dicom-viewer);
    opacity: 0.95;
    transform: translate(-50%, -50%);
    box-shadow: 1px 1px 15px #000;
    font-size: 16px;

    .popup-info-title-bar {
      position: relative;
      text-align: center;
      line-height: 40px;
      box-shadow: 0px 1px 19px #000;

      .popup-info-close {
        position: absolute;
        right: 10px;
        top: 0;
        font-size: 12px;
        .pointer();
      }
    }

    .popup-info-content {
      padding: 20px 40px 40px;
      height: 55vh;
      overflow-y: auto;
      line-height: 30px;
      opacity: 0.9;

      .title {
        color: #8eda95;
        padding-top: 14px;
        font-size: 18px;
      }

      .DICOM-meta-data-cell {
        display: flex;
        .pointer();

        .label {
          width: 110px;
        }

        .value {
          flex: 1;
          overflow: auto;
          text-align: right;
        }
      }

      .about-Us {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;

        .about-Us-cell {
          text-align: center;
        }
      }
    }
  }
}

.player-bar {
  position: absolute;
  left: 50%;
  bottom: 10px;
  transform: translateX(-50%);
  height: 46px;
  background: rgb(0, 0, 0);
  z-index: 9;
  border: 1px solid rgb(51, 255, 255);
  border-radius: 6px;
  padding: 0 10px;
  display: flex;
  align-items: center;

  .control {
    color: gray;
    font-size: 20px;
    padding: 6px;
    .pointer()
  }

  .text {
    font-size: 16px;
    font-family: "Microsoft YaHei";
    margin-left: 10px;
    cursor: default;
    color: rgb(255, 255, 255);
  }

  input {
    height: 18px;
    width: 35px;
    margin-left: 5px;
    pointer-events: auto;
    background-color: #fff;
    color: #000;
  }
}